$responsive-breakpoint: 40rem;

.modal-container {
  // use text-align for center aligning instead of margin auto
  // because we want the "modal" box width be determined by it's children
  // ie the "modal__content"
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  overflow-y: scroll;
  overflow-x: hidden;
  // https://stackoverflow.com/questions/37914983/overflow-scrolling-not-working-on-position-fixed-element-ios
  -webkit-overflow-y-scrolling: touch;
}

.modal {
  display: inline-flex;
  flex-direction: column;

  --container-padding: 2rem;
  --spacing-vertical: 2rem;
  --spacing-horizontal: 1rem;

  // define margin variables for the child to use
  --margin-horizontal: 2rem;
  --margin-vertical: 2rem;

  // modal sizes
  --modal-content-width: 30rem;
  --modal-content-width--narrow: 20rem;
  --modal-content-width--wide: 40rem;

  // max-width: 40rem;

  // center the modal
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 0rem;

  @media only screen and (min-width: $responsive-breakpoint) {
    --margin-horizontal: 3rem;
    --margin-vertical: 3rem;

    // center the modal
    margin-left: 0rem;
    margin-right: 0rem;
    margin-top: 15vh;
    margin-bottom: 15vh;
  }

  // reset the text align setting from parent
  text-align: left;

  // padding for all the content
  padding: 0rem;

  // set up a white sheet backdrop
  background-color: var(--color-primary-lightest);
  box-shadow: var(--drop-shadow-dark);

  &::before {
    content: "";
    display: block;
    background-color: var(--color-accent);
    height: 4px;
    max-width: 4rem;

    // offset the left hand side
    margin-left: var(--margin-horizontal);

    margin-bottom: 1rem;
  }

  // entering animation
  animation-name: modal-animation--enter;
  animation-duration: 700ms;
  animation-timing-function: cubic-bezier(0.87, 0, 0.13, 1);
  animation-fill-mode: forward;
  animation-iteration-count: 1;
}

.modal__content {
  // mawidth: var(--modal-content-width);

  margin: var(--margin-vertical) var(--margin-horizontal);

  // vertical flow of the layout
  & > * {
    margin-bottom: var(--spacing-vertical);
  }
  & > *:first-child {
    margin-top: 0rem;
  }
  & > *:last-child {
    margin-bottom: 0rem;
  }

  // spacing for all the navigation form controls
  & > a {
    margin-right: var(--spacing-horizontal);
    // dirty hack to remove all the margin under the buttons
    margin-bottom: 0rem;
  }
}

@media only screen and (min-width: $responsive-breakpoint) {
  .modal__content--wide {
    width: var(--modal-content-width--wide);
  }

  .modal__content--medium {
    width: var(--modal-content-width);
  }

  .modal__content--narrow {
    width: var(--modal-content-width--narrow);
  }
}
// temporarily set the backdrop
body {
  background: var(--color-primary-darker);
}

.modal__content .lead {
  margin-top: 0rem;
  margin-bottom: 4rem;
}

@keyframes modal-animation--enter {
  0% {
    opacity: 0;
    // transform: translateY(10%);
  }
  100% {
    opacity: 1;
    // transform: translateY(0%);
  }
}
