.autocomplete-field {
  position: relative;
}

.autocomplete-field__list {
  --spacing-vertical: 0.75rem;
  --spacing-horizontal: 0.75rem;

  position: absolute;
  top: 2.35rem;
  left: 0rem;

  // min-width: 30rem;

  list-style: none;

  // visual appearance of the drop down list
  background-color: var(--color-primary-lightest);
  box-shadow: var(--drop-shadow-light);

  li {
    cursor: pointer;

    margin: 0rem;
    padding: var(--spacing-vertical) var(--spacing-horizontal);

    border-left: 2px solid transparent;
  }

  li:hover,
  li.highlighted {
    background-color: var(--color-primary-lighter);
    border-left: 2px solid var(--color-accent);
  }
}

.autocomplete-field__list--scrollable {
  overflow-y: scroll;
  max-height: 20rem;
}

.autocomplete-field__list--hidden {
  display: none;
}
