.textarea {
  background-color: var(--color-primary-lighter);
  width: 100%;

  position: relative;
}

.textarea__text {
  background-color: var(--color-primary-lighter);

  width: 100%;
  padding: 1rem var(--margin-horizontal) 4rem; // extra padding bottom for wordcount

  // remove the border
  border: none;

  // only allow vertical resize of the text area
  resize: vertical;

  // hide the blue line
  appearance: none;
  outline-width: 0;

  // copy from main code
  font-family: var(--typeface-body);
  font-size: var(--type-size-body);
  letter-spacing: var(--type-tracking-body);
  line-height: var(--type-leading-body);
  font-weight: normal;
}

.textarea__word-count {
  color: var(--color-primary-light);

  position: absolute;
  bottom: 1rem;
  left: var(--margin-horizontal);

  pointer-events: none;
}
